const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			cityInfo: {},
			see_sticky: '',
			list: [
				// {avatar: 'https://jinrong.beaconway.cn/uploads/files/images/202004/01/1_1585715979_KEJLhcJznd.jpg',name: '来来来',title: 'nxjvnjdxflxzfkjbig',created_at: '2018-10-20',type: '0'},
				// {avatar: 'https://jinrong.beaconway.cn/uploads/files/images/202004/01/1_1585715979_KEJLhcJznd.jpg',name: '来来来',title: 'nxjvnjdxflxzfkjbig',created_at: '2018-10-20',type: '1'},
				// {avatar: 'https://jinrong.beaconway.cn/uploads/files/images/202004/01/1_1585715979_KEJLhcJznd.jpg',name: '来来来',title: 'nxjvnjdxflxzfkjbig',created_at: '2018-10-20',type: '0'},
			],
			page: '1',
			pageList: '1',
			tipList: [],
			total: '',
			is_follow: '',
			token: '',
			inputValue: '',
			userInfo: {}
		};
	},
	onLoad(e) {
		console.log(e);
		this.token = uni.getStorageSync('token');
		this.imgUrl = helper.imgUrl;
		uni.setNavigationBarTitle({
			title: e.title
		});
		this.cityInfo = e;
		this.getList();
		this.see_stickyList();
		this.user();
	},
	methods: {
		user() {
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					this.userInfo = res.data.id;
					console.log(this.userInfo, '+++++---------------------+++');
				}
			});
		},
		// 发布
		getPost() {
			uni.navigateTo({
				url: `/pages/post?id=${this.cityInfo.id}`
			});
		},
		// 置顶加载更多
		getMore() {
			this.page++;
			this.see_stickyList();
		},
		onReachBottom() {
			this.pageList++;
			this.getList();
		},
		// 跳转帖子详情
		// getDateil(e) {
		// 	uni.navigateTo({
		// 		url: `/pages/articleDetail?id=${e}`
		// 	});
		// },
		inputV(e) {
			console.log(e);
			this.inputValue = e.detail.value;
		},
		postContent() {
			if (this.inputValue == '') {
				uni.showToast({
					title: '请输入发表内容',
					icon: 'none'
				});
				return false;
			}
			uni.request({
				url: `${helper.requestUrl}/message/sendMessageWithCityId`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					city_id: this.cityInfo.id,
					content: this.inputValue
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '**************');

					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
						this.list = [];
						this.pageList = '1';
						this.inputValue = '';
						this.getList();
					}
				}
			});
		},
		// 帖子列表
		getList() {
			uni.request({
				url: `${helper.requestUrl}/message/getMessageByCityId`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					city_id: this.cityInfo.id,
					page: this.pageList,
					page_size: '10'
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');

					if (res.data.status_code == 200) {
						console.log(res.data.data.data, '/////////////////////////////////');
						this.list = this.list.concat(res.data.data.data);
						this.is_follow = res.data.is_follow;
					}
				}
			});
		},
		see_stickyList() {
			uni.request({
				url: `${helper.requestUrl}/posts/city-posts`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				data: {
					city_id: this.cityInfo.id,
					see_sticky: '1', //置顶
					page: this.page,
					page_size: '3'
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '++++++++');
					if (res.data.status_code == 200) {
						this.total = res.data.total;
						this.tipList = this.tipList.concat(res.data.data);
					}
				}
			});
		},
		addFollow(type) {
			// 关注用户
			uni.request({
				url: `${helper.requestUrl}/user/add_follow`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					follow_id: this.cityInfo.id,
					type: type
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						});
						this.is_follow = 1;
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
